import { AfterViewInit, Component } from '@angular/core';
import { AgTreeGridImpl, AgTreeGridOptions, AgTreeGridEvent} from './ag-tree-grid/interfaces/index';
import { AgTreeGridEvents } from './ag-tree-grid/enums/index';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'my-app',
  moduleId: module.id,
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.css']
})
export class AppComponent implements AfterViewInit, AgTreeGridImpl {


  options: AgTreeGridOptions;


  onEvents(event: AgTreeGridEvent): void {
    switch(event.type) {
      case AgTreeGridEvents.selectBubble:
        console.debug('select bubble'); 
        console.debug(event.data.api.getSelectedRows());
        break;
    }

  }

  constructor(private _http: HttpClient) {
    this.options = {
      columnDefs: [
        {
          headerName: 'Gold',
          field: 'gold',
          width: 100,
          aggFunc: 'sum',
          hide: true
        },
        {
          headerName: 'Silver',
          field: 'silver',
          width: 100,
          aggFunc: 'sum',
          hide: true
        },
        {
          headerName: 'Bronze',
          field: 'bronze',
          width: 100,
          aggFunc: 'sum',
          hide: true
        },
        {
          headerName: 'Total',
          field: 'total',
          width: 100,
          aggFunc: 'sum',
          hide: true
        },
        {
          headerName: 'Age',
          field: 'age',
          width: 90,
          checkboxSelection: true,
          aggFunc: 'sum',
          hide: true
        },
        {
          headerName: 'Country',
          field: 'country',
          width: 120,
          rowGroupIndex: 0,
          hide: true
        },
        {
          headerName: 'Year',
          field: 'year',
          width: 90,
          hide: true
        },
        {
          headerName: 'Date',
          field: 'date',
          width: 110,
          hide: true
        },
        {
          headerName: 'Sport',
          field: 'sport',
          width: 110,
          rowGroupIndex: 1,
          hide: true
        }
      ]
    }


  }

  ngAfterViewInit(): void {
    this._http
      .get(
        './assets/data.json'
      )
      .subscribe((data: any) => {
        this.options.rowData = data;
      });
  }

}

